import { Accordion, BodyShort, Heading } from '@navikt/ds-react';
import React, { useState } from 'react';

export const InnsynsrettAccordionItem = () => {
    const [open, setOpen] = useState(false);

    function handleClick() {
        setOpen(!open);
    }

    return (
        <Accordion.Item>
            <Accordion.Header onClick={handleClick}>
                <Heading level="2" size="small">
                    Er du under 18 r?
                </Heading>
            </Accordion.Header>
            <Accordion.Content>
                <Heading level="3" size="xsmall" className="pt-4">
                    Er du under 18 r?
                </Heading>
                <BodyShort className="pb-4">
                    Nr du er under 18 r s har dine foresatte rett til  be om  f se alt du skriver i Nav sine
                    tjenester. Derfor br du ikke skrive noe her som du ikke vil at foresatte dine skal lese.
                </BodyShort>
                <BodyShort>
                    Nr du blir 18 r vil de ikke kunne lese nye ting du skriver, men de kan fortsatt lese det du skrev
                    fr du fylte 18 r.
                </BodyShort>
            </Accordion.Content>
        </Accordion.Item>
    );
};
